import settingmodel from '../../components/settings/settingmodel.vue'
import uniIcons from "@/components/uni-icons/uni-icons.vue"
export default {
	name: "",
	components: {
		settingmodel,
		uniIcons
	},
	props: {},
	data() {
		return {
			// 接收存储在缓存中的用户信息
			// userObj: {}
		}
	},
	methods: {
		// 跳转到修改头像的页面
		changeImage() {
			uni.navigateTo({
				url: '../portraitupload/portraitupload',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		// 清理缓存按钮
		shows() {
			uni.showToast({
				title: '该功能暂未开发~',
				icon: "none"
			});
		},
		// 退出登录
		loginOut() {
			uni.request({
				url: this.$api.loginOut({
					userId: this.$store.state.userObj.id
				}),
				method: 'POST',
				data: {},
				success: res => {
					uni.removeStorageSync('userinfo')
					uni.removeStorageSync('userToken')
					this.$store.state.userObj = {}
					uni.navigateBack({
						delta: 1
					});
					uni.showToast({
						title: '退出成功'
					});
				},
				fail: () => {},
				complete: () => {}
			});
		}
	},
	mounted() {},
	onLoad() {
		if (uni.getStorageSync('userinfo')) {
			this.$store.state.userObj = JSON.parse(uni.getStorageSync('userinfo'))
			console.log(this.$store.state.userObj);
		}
	},
	filters: {

	},
	computed: {

	},
	watch: {

	},
	directives: {

	}
}
